*{
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
  /* overflow-x: hidden; */
}
.header{
  text-align: center;
  width: 75%;
  margin: 0 auto;
  font-size: 55px;
  padding: 50px;
  /* border-bottom: 10px solid black; */

}
.joke-box{
  width: 75%;
}
.App{
  background-color: lightcoral;
}
.Joke-container{
  display: flex;
}
.Joke1{
  background-color: lightblue;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 400px;
  flex-direction: column;
  width: 50%;
  text-align: center;
  /* border-bottom: 10px solid lightcoral; */
  border: 5px solid lightcoral;
  border-radius: 25px;

}
.Joke2{
  background-color: lightblue;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 400px;
  flex-direction: column;
  width: 50%;
  text-align: center;
  border: 5px solid lightcoral;
  border-radius: 25px;


}
.button{
  border: none;
  background-color: black;
  color: white;
  padding: 20px;
  margin-top: 25px;
  border-radius: 25px;

}
.button:hover{
  cursor: pointer;
}
.button:focus{
  outline: none;
}
.joke-head{
  margin-bottom: 25px;
}